/* reset */

html,
* {
  margin: 0;
  padding: 0;
}

.asteroid {
  position: absolute;
}

.test {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
}

.timeline-container {
  display: flex;
  flex-direction: column;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hide {
  opacity: 0;
}

.show {
  transition: 0.5s;
  opacity: 1;
}

.timeline-item {
  display: flex;
  justify-content: flex-end;
  padding-right: 30px;
  margin: 100px 0;
  width: 44%;
}

.timeline-item:nth-child(odd) {
  display: flex;
  align-self: flex-end;
  justify-content: flex-start;
  padding-right: 0px;
  padding-left: 30px;
}

.timeline-item-content {
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  align-items: flex-end;
  padding: 15px;
  text-align: right;
  max-width: 70%;
}

.timeline-item:nth-child(odd) .timeline-item-content {
  align-items: flex-start;
  text-align: left;
}

.rocket {
  display: flex;
  justify-content: center;
  align-items: center;
}

img {
  height: 150px;
}

.container {
  height: 300vh;
  background-image: linear-gradient(
    -20deg,
    #ddd6f3 0%,
    #faaca8 100%,
    #faaca8 100%
  );
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}
